.containerParent {
  display: grid;
  grid-template-areas: ". container .";
  grid-template-columns: 7% 86% 7%;
}
.containerChild {
  grid-area: container;
}
body .catalog {
  padding-top: 25px;
  width: 100%;
  display: grid;
  grid-template-areas: ". container .";
  grid-template-columns: 7% 86% 7%;
}
body .catalog .container {
  grid-area: container;
  display: grid;
  grid-template-areas: "aside breadcrumb" "aside catalog" "order order";
  grid-template-columns: 25% 75%;
}
body .catalog .container .content__aside {
  grid-area: aside;
}
body .catalog .container .content__catalog__breadcrumb {
  grid-area: breadcrumb;
}
body .catalog .container .content__catalog__breadcrumb ul {
  display: flex;
}
body .catalog .container .content__catalog__breadcrumb ul li a {
  font: 0.875em/1em "PTSans-Regular";
  color: #474747;
}
body .catalog .container .content__catalog__breadcrumb ul li span {
  font: 0.875em/1em "PTSans-Regular";
  color: #474747;
}
body .catalog .container .content__catalog {
  grid-area: catalog;
}
body .catalog .container .content__catalog h5 {
  grid-area: headline;
  font: 2.25em/2.375em "PTSerif-Italic";
  color: #0e3a40;
}
body .catalog .container .content__catalog .catalog__options {
  display: flex;
  justify-content: space-between;
}
body .catalog .container .content__catalog .catalog__options__goods,
body .catalog .container .content__catalog .catalog__options__sortBy {
  display: flex;
  align-items: center;
  position: relative;
}
body .catalog .container .content__catalog .catalog__options__goods p,
body .catalog .container .content__catalog .catalog__options__sortBy p {
  font: 0.875em/1em "PTSans-Regular";
}
body .catalog .container .content__catalog .catalog__options__goods p span,
body .catalog .container .content__catalog .catalog__options__sortBy p span {
  font: 1em/1em "PTSans-Bold";
}
body .catalog .container .content__catalog .catalog__options__goods select,
body .catalog .container .content__catalog .catalog__options__sortBy select {
  margin: 0 0 0 10px;
  padding: 0 0 0 15px;
  width: 200px;
  height: 30px;
  font: 0.875em/30px "PTSans-Bold";
  color: #00929e;
  background: #fff url("../img/down-arrow.png") no-repeat 96% center;
  border: 1px solid #e6e6e6;
  border-radius: 5px;
  outline: none;
  appearance: none!important;
}
body .catalog .container .content__catalog .catalog__options__goods select option,
body .catalog .container .content__catalog .catalog__options__sortBy select option {
  border: 1px solid #e6e6e6;
  border-radius: 5px;
  outline: none;
}
body .catalog .container .content__catalog .catalog__options__goods select:hover,
body .catalog .container .content__catalog .catalog__options__sortBy select:hover {
  cursor: pointer;
}
body .catalog .container .content__catalog .catalog__options .mobile {
  display: none;
  align-items: center;
}
body .catalog .container .content__catalog .catalog__options .mobile p {
  font: 0.875em/1em "PTSans-Regular";
}
body .catalog .container .content__catalog .catalog__options .mobile p span {
  font: 1em/1em "PTSans-Bold";
}
body .catalog .container .content__catalog .catalog__options .mobile .mobileSort {
  margin: 0 0 0 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  font: 1.25em/1.375em "PTSans-Regular";
  color: #00929e;
  text-decoration: none;
}
body .catalog .container .content__catalog .catalog__options .mobile .mobileSort:hover {
  cursor: pointer;
}
body .catalog .container .content__catalog .catalog__goods {
  display: flex;
  flex-wrap: wrap;
}
body .catalog .container .content__catalog .catalog__goods div {
  margin: 15px;
  width: 225px;
  height: 350px;
  position: relative;
  text-align: center;
  border: 1px solid #e6e6e6;
  border-radius: 5px;
  box-sizing: border-box;
}
body .catalog .container .content__catalog .catalog__goods div a {
  display: block;
  position: relative;
  text-decoration: none;
}
body .catalog .container .content__catalog .catalog__goods div a img {
  margin-top: 15px;
  width: 200px;
  height: 200px;
}
body .catalog .container .content__catalog .catalog__goods div a p {
  padding: 10px;
  font: 1em/1.125em "PTSans-Regular";
  color: #0e3a40;
  text-align: left;
}
body .catalog .container .content__catalog .catalog__goods div a .price {
  font: 1.25em/1.375em "PTSans-Bold";
}
body .catalog .container .content__catalog .catalog__goods div a .price span {
  font: 0.875em/1em "PTSans-Regular";
}
body .catalog .container .content__catalog .catalog__goods div a .favorite {
  position: absolute;
  right: 10px;
  bottom: 0;
}
body .catalog .container .content__catalog .catalog__goods div a .favorite i {
  font-size: 1.375em;
  color: #00929e;
}
body .catalog .container .content__catalog .catalog__goods div .hover {
  padding: 0;
  margin: 0;
  width: 100.75%;
  height: 50px;
  display: none;
  flex-direction: row-reverse;
  justify-content: center;
  align-items: flex-end;
  position: absolute;
  left: -1px;
  bottom: -25px;
  z-index: 2;
  background-color: #fff;
  border: 1px solid #e6e6e6;
  border-top: none;
}
body .catalog .container .content__catalog .catalog__goods div .hover .addBusket,
body .catalog .container .content__catalog .catalog__goods div .hover .addFavorite {
  margin: 25px 5px 10px;
  display: block;
  height: 40px;
  position: relative;
  font: 18px/40px "PTSans-Regular";
  border: none;
  border-radius: 5px;
  outline: none;
  z-index: 1;
}
body .catalog .container .content__catalog .catalog__goods div .hover .addBusket:hover,
body .catalog .container .content__catalog .catalog__goods div .hover .addFavorite:hover {
  cursor: pointer;
}
body .catalog .container .content__catalog .catalog__goods div .hover .addBusket {
  width: 150px;
  color: #fff;
  background-color: #00929e;
}
body .catalog .container .content__catalog .catalog__goods div .hover .addBusket span {
  margin: 0 0 0 10px;
}
body .catalog .container .content__catalog .catalog__goods div .hover .addFavorite {
  width: 40px;
  color: #0e3a40;
  background-color: #fff;
  border: 1px solid #0e3a40;
  box-sizing: border-box;
}
body .catalog .container .content__catalog .catalog__goods div:hover {
  cursor: pointer;
}
body .catalog .container .content__catalog .catalog__goods div:hover .hover {
  display: flex;
  cursor: default;
}
body .catalog .container .content__catalog .catalog__showMore {
  width: 81%;
  margin: 15px 0 50px;
  display: flex;
  justify-content: center;
}
body .catalog .container .content__catalog .catalog__showMore a {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 225px;
  height: 40px;
  margin: 15px 50px;
  font: 1em/40px "PTSans-Regular";
  color: #176975;
  text-decoration: none;
  background-color: transparent;
  border: 1px solid #176975;
  border-radius: 5px;
}
body .catalog .container .content__catalog .catalog__showMore a i {
  font-size: 1.25em;
  line-height: 40px;
}
body .catalog .container .content__catalog .catalog__showMore a:hover {
  color: #fff;
  background-color: #176975;
  cursor: pointer;
  transition: .5s;
}
body .catalog .container .content__catalog .catalog__paginator {
  width: 81%;
  display: flex;
  justify-content: center;
}
body .catalog .container .content__catalog .catalog__paginator ul {
  display: flex;
}
body .catalog .container .content__catalog .catalog__paginator ul li a {
  margin: 0 5px;
  display: block;
  width: 40px;
  height: 40px;
  font: 1em/40px "PTSans-Regular";
  color: #00929e;
  text-align: center;
  text-decoration: none;
  border: 1px solid #00929e;
  border-radius: 5px;
}
body .catalog .container .content__catalog .catalog__paginator ul li a:hover {
  background-color: #f6f6f6;
}
body .catalog .container .content__catalog .catalog__paginator ul li .active {
  background-color: #f6f6f6;
}
body .catalog .container .content__catalog .catalog__paginator .mobile {
  display: none;
}
body .catalog .container .content__catalog .catalog__paginator .mobile li:nth-child(3) a {
  font-size: 2em;
  border: none;
  user-select: none;
}
body .catalog .container .content__catalog .catalog__paginator .mobile li:nth-child(3) a:hover {
  cursor: default;
  background-color: #fff;
}
body .catalog .container .content__catalog .catalog__aboutCategory {
  margin: 70px 0 0;
  width: 65%;
}
body .catalog .container .content__catalog .catalog__aboutCategory h5 {
  grid-area: headline;
  font: 2.25em/2.375em "PTSerif-Italic";
  color: #0e3a40;
}
body .catalog .container .content__catalog .catalog__aboutCategory p {
  margin: 0 0 20px;
  font-size: 0.88888889em "PTSans-Regular";
}
body .catalog .container .content__howToMakeOrder {
  grid-area: order;
}
body .catalog__options__mobile {
  padding: 15px 0 25px;
  width: 450px;
  position: relative;
  background-color: #fff;
  border-radius: 5px;
}
body .catalog__options__mobile #mobileSortClose {
  width: 21px;
  height: 21px;
  display: block;
  position: absolute;
  top: 10px;
  right: 10px;
  text-align: right;
  color: #00929e;
  cursor: pointer;
}
body .catalog__options__mobile ul li {
  display: flex;
  flex-direction: column;
}
body .catalog__options__mobile ul li h6 {
  margin: 15px 15px 0;
  padding: 0 25px 0 20px;
  font: 1.375em/40px "PTSans-Bold";
  color: #fff;
  background-color: #00929e;
  border-radius: 5px;
}
body .catalog__options__mobile ul li label {
  margin: 0 15px;
  padding: 0 25px 0 20px;
  border-top: 1px solid #cecece;
  border-bottom: 1px solid #cecece;
}
body .catalog__options__mobile ul li label p {
  margin: 5px 0 0 15px;
  display: inline-block;
  font: 1.375em/1.5em "PTSans-Regular";
  color: #0e3a40;
}
body .catalog__options__mobile ul li label:first-child {
  border-top: none;
}
body .catalog__options__mobile ul li label:last-child {
  border-bottom: none;
}
body .catalog__options__mobile ul li label:hover {
  cursor: pointer;
}
body .catalog__options__mobile ul li input[type="radio"] {
  display: none;
}
body .catalog__options__mobile ul li input[type="radio"] + label span {
  display: inline-block;
  width: 19px;
  height: 19px;
  margin: -1px 4px 0 0;
  vertical-align: middle;
  background: #fff;
  cursor: pointer;
}
body .catalog__options__mobile ul li input[type="radio"]:checked + label span {
  background: url(../img/checked.png) 0 top no-repeat;
  background-size: 100%;
}
body .catalog__options__mobile div {
  margin: 35px 15px 5px;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
body .catalog__options__mobile div a {
  width: 45%;
  display: block;
  font: 1.25em/40px "PTSans-Regular";
  color: #fff;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  background: #00929e;
  border-radius: 5px;
}
