@import "../components/variables";
@import "../components/mixin";

body{
  .catalog{
    padding-top: 25px;
    width: 100%;
    .containerParent();
    .container{
      .containerChild();
      display: grid;
      grid-template-areas: "aside breadcrumb"
                           "aside catalog"
                           "order order";
      grid-template-columns: 25% 75%;
      .content__aside{
        grid-area: aside;
      }
      .content__catalog__breadcrumb{
        grid-area: breadcrumb;
        ul {
          display: flex;
          li {
            a {
              font: .875em/1em @sansregular;
              color: #474747;
            }
            span {
              font: .875em/1em @sansregular;
              color: #474747;
            }
          }
        }
      }
      .content__catalog{
        grid-area: catalog;
        h5 {
          grid-area: headline;
          font: 2.25em/2.375em @serifitalic;
          color: @linkColor;
        }
        .catalog__options{
          display: flex;
          justify-content: space-between;
          &__goods,
          &__sortBy{
            display: flex;
            align-items: center;
            position: relative;
            p{
              font: .875em/1em @sansregular;
              span{
                font: 1em/1em @sansbold;
              }
            }
            select{
              margin: 0 0 0 10px;
              padding: 0 0 0 15px;
              width: 200px;
              height: 30px;
              font: .875em/30px @sansbold;
              color: @mainColor;
              background:#fff url("../img/down-arrow.png") no-repeat 96% center;
              border: 1px solid #e6e6e6;
              border-radius: 5px;
              outline: none;
              appearance: none!important;
              option{
                border: 1px solid #e6e6e6;
                border-radius: 5px;
                outline: none;
              }
              &:hover{
                cursor: pointer;
              }
            }
          }
          .mobile{
            display: none;
            align-items: center;
            p{
              font: .875em/1em @sansregular;
              span{
                font: 1em/1em @sansbold;
              }
            }
            .mobileSort{
              margin: 0 0 0 50px;
              display: flex;
              justify-content: center;
              align-items: center;
              font: 1.25em/1.375em @sansregular;
              color: @mainColor;
              text-decoration: none;
              &:hover{
                cursor: pointer;
              }
            }
          }
        }
        .catalog__goods{
          display: flex;
          flex-wrap: wrap;
          div {
            margin: 15px;
            width: 225px;
            height: 350px;
            position: relative;
            text-align: center;
            border: 1px solid #e6e6e6;
            border-radius: 5px;
            box-sizing: border-box;
            a {
              display: block;
              position: relative;
              text-decoration: none;
              img {
                margin-top: 15px;
                width: 200px;
                height: 200px;
              }
              p {
                padding: 10px;
                font: 1em/1.125em @sansregular;
                color: @linkColor;
                text-align: left;
              }
              .price {
                font: 1.25em/1.375em @sansbold;
                span {
                  font: .875em/1em @sansregular
                }
              }
              .favorite{
                position: absolute;
                right: 10px;
                bottom: 0;
                i{
                  font-size: 1.375em;
                  color: @mainColor;
                }
              }
            }
            .hover{
              padding: 0;
              margin: 0;
              width: 100.75%;
              height: 50px;
              display: none;
              flex-direction: row-reverse;
              justify-content: center;
              align-items: flex-end;
              position: absolute;
              left: -1px;
              bottom: -25px;
              z-index: 2;
              background-color: #fff;
              border: 1px solid #e6e6e6;
              border-top: none;
              .addBusket,
              .addFavorite{
                margin: 25px 5px 10px;
                display: block;
                height: 40px;
                position: relative;
                font: 18px/40px @sansregular;
                border: none;
                border-radius: 5px;
                outline: none;
                z-index: 1;
                &:hover{
                  cursor: pointer;
                }
              }
              .addBusket{
                width: 150px;
                color: #fff;
                background-color: @mainColor;
                span{
                  margin: 0 0 0 10px;
                }
              }
              .addFavorite{
                width: 40px;
                color: @linkColor;
                background-color: #fff;
                border: 1px solid @linkColor;
                box-sizing: border-box;
              }
            }
            &:hover {
              cursor: pointer;
              .hover{
                display: flex;
                cursor: default;
              }
            }
          }
        }
        .catalog__showMore{
          width: 81%;
          margin: 15px 0 50px;
          display: flex;
          justify-content: center;
          a {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 225px;
            height: 40px;
            margin: 15px 50px;
            font: 1em/40px @sansregular;
            color: #176975;
            text-decoration: none;
            background-color: transparent;
            border: 1px solid #176975;
            border-radius: 5px;
            i{
              font-size: 1.25em;
              line-height: 40px;
            }
            &:hover {
              color: #fff;
              background-color: #176975;
              cursor: pointer;
              transition: .5s;
            }
          }
        }
        .catalog__paginator{
          width: 81%;
          display: flex;
          justify-content: center;
          ul{
            display: flex;
            li{
              a{
                margin: 0 5px;
                display: block;
                width: 40px;
                height: 40px;
                font: 1em/40px @sansregular;
                color: @mainColor;
                text-align: center;
                text-decoration: none;
                border: 1px solid @mainColor;
                border-radius: 5px;
                &:hover{
                  background-color: #f6f6f6;
                }
              }
              .active{
                background-color: #f6f6f6;
              }
            }
          }
          .mobile{
            display: none;
            li{
              &:nth-child(3){
                a{
                  font-size: 2em;
                  border: none;
                  user-select: none;
                  &:hover{
                    cursor: default;
                    background-color: #fff;
                  }
                }
              }
            }
          }
        }
        .catalog__aboutCategory{
          margin: 70px 0 0;
          width: 65%;
          h5 {
            grid-area: headline;
            font: 2.25em/2.375em @serifitalic;
            color: @linkColor;
          }
          p{
            margin: 0 0 20px;
            font-size: 1em/1.125em @sansregular;
          }
        }
      }
      .content__howToMakeOrder{
        grid-area: order;
      }
    }
  }
  .catalog__options__mobile{
    padding: 15px 0 25px;
    width: 450px;
    position: relative;
    background-color: #fff;
    border-radius: 5px;
    #mobileSortClose{
      width: 21px;
      height: 21px;
      display: block;
      position: absolute;
      top: 10px;
      right: 10px;
      text-align: right;
      color: @mainColor;
      cursor: pointer;
    }
    ul{
      li{
        display: flex;
        flex-direction: column;
        h6{
          margin: 15px 15px 0;
          padding: 0 25px 0 20px;
          font: 1.375em/40px @sansbold;
          color: #fff;
          background-color: @mainColor;
          border-radius: 5px;
        }
        label{
          margin: 0 15px;
          padding: 0 25px 0 20px;
          border-top: 1px solid #cecece;
          border-bottom: 1px solid #cecece;
          p{
            margin: 5px 0 0 15px;
            display: inline-block;
            font: 1.375em/1.5em @sansregular;
            color: @linkColor;
          }
          &:first-child{
            border-top: none;
          }
          &:last-child{
            border-bottom: none;
          }
          &:hover{
            cursor: pointer;
          }
        }
        input[type="radio"]{
          display: none;
        }
        input[type="radio"] + label span {
          display: inline-block;
          width: 19px;
          height: 19px;
          margin: -1px 4px 0 0;
          vertical-align: middle;
          background: #fff;
          cursor: pointer;
        }
        input[type="radio"]:checked + label span {
          background:url(../img/checked.png) 0 top no-repeat;
          background-size: 100%;
        }
      }
    }
    div{
      margin: 35px 15px 5px;
      display: flex;
      justify-content: space-around;
      align-items: center;
      a{
        width: 45%;
        display: block;
        font: 1.25em/40px @sansregular;
        color: #fff;
        text-align: center;
        text-decoration: none;
        text-transform: uppercase;
        background: @mainColor;
        border-radius: 5px;
      }
    }
  }
}